  font-weight: 400;
  line-height: 27px;
  color: #333333;
  margin-bottom: 30px;
  max-width: 630px;
}
.block-contacts__text ul li {
  padding-left: 24px;
  position: relative;
}
.block-contacts__text ul li::before {
  content: "";
  position: absolute;
  left: 8px;
  top: 50%;
  transform: translateY(-50%);
  width: 3px;
  height: 3px;
  flex-shrink: 0;
  background-color: #333333;
  border-radius: 50%;
}
.block-contacts__text b,
.block-contacts__text strong {
  font-weight: 700;
}
.block-contacts__info {
  border: 1px solid #E7E7E7;
  background-color: rgba(245, 245, 246, 0.6);
  border-radius: 12px;
  padding: 32px 50px;
  columns: 2;
  column-gap: 30px;
}
.block-contacts__item {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  column-gap: 20px;
  page-break-inside: avoid;
  -moz-column-break-inside: avoid;
  -webkit-column-break-inside: avoid;
  break-inside: avoid;
  break-inside: avoid-column;
}
.block-contacts__item:not(:last-child) {
  margin-bottom: 20px;
}
.block-contacts__item-icon {
  width: 25px;
  height: auto;
  flex-shrink: 0;
}
.block-contacts__item-name {
  font-size: 18px;
  font-weight: 700;
  line-height: 20px;
  color: #4F649A;
  margin-bottom: 10px;
}
.block-contacts__item span,
.block-contacts__item a {
  font-family: "Circe";
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: #919191;
  display: block;
}
.block-contacts__item span:not(:last-child),
.block-contacts__item a:not(:last-child) {
  margin-bottom: 10px;
}
.block-contacts__item span b,
.block-contacts__item span strong,
.block-contacts__item a b,
.block-contacts__item a strong {
  font-weight: 700;
}
.block-contacts__map {
  width: 100%;
  height: 644px;
  border-radius: 12px;
  flex-shrink: 0;
  overflow: hidden;
}
.block-contacts .map {
  width: 100%;
  height: 100%;
}
.block-contacts [class*=copyrights-pane] {
  display: none !important;
}

.block-other {
  background-color: #F1F5FF;
  padding-top: 70px;
  padding-bottom: 50px;
}
.block-other__title {
  margin-bottom: 20px;
}
.block-other .swiper-scrollbar {
  background-color: #ffffff;
}

.catalog {
  padding-top: 50px;
  padding-bottom: 100px;
}
.catalog .breadcrumbs {
  margin-top: 0;
}
.catalog__container {
  display: grid;
  grid-template-columns: 1fr 2.5fr;
}
.catalog-content__sort {
  border: 1px solid #EFEFEF;
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  color: #919191;
  border: 1px solid #EFEFEF;
  background-color: #F8F8F8;
  height: 40px;
  padding: 9px 37px 9px 20px;
  border-radius: 4px;
  margin-bottom: 30px;
}
.catalog-content__items {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 16px;
  row-gap: 26px;
}
.catalog-content__button {
  grid-column: 1/5;
  font-size: 14px;
  font-weight: 700;
  line-height: 22px;
  background: #333333;
  background-color: #F1F5FF;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  cursor: pointer;
}
.catalog-content__pagination {
  grid-column: 1/5;
  margin-top: -6px;
}

.card__container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  column-gap: 100px;
  margin-bottom: 70px;
}
.card-content {
  width: 100%;
}
.card-content__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 6px;
}
.card-content__subname {
  font-size: 16px;
  font-weight: 700;
  line-height: 22px;
  color: #4F649A;
}
.card-content__status {
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  color: #919191;
}
.card-content__status.check {
  color: #4CAF50;
}
.card-content__name {
  font-size: 16px;
  font-weight: 700;
  line-height: 22px;
  color: #333333;
  margin-bottom: 6px;
}
.card-content__info {
  display: flex;
  align-items: center;
  column-gap: 18px;
  margin-bottom: 8px;
}
.card-content__vendor {
  display: flex;
  align-items: center;
  column-gap: 5px;
}
.card-content__vendor-name {
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  color: #999999;
}
.card-content__rating {
  display: flex;
  align-items: center;
  column-gap: 10px;
}
.card-content__count-reviews {
  font-family: "Gilroy";
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  color: #999999;
}
.card-content__size {
  border-top: 1px solid #E7E7E7;
  padding-top: 22px;
  margin-bottom: 95px;
}
.card-content__size-name {
  font-family: "Gilroy";
  font-size: 16px;
  font-weight: 600;
  line-height: 18px;
  color: #333333;
  margin-bottom: 12px;
}
.card-content__size-items {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}
.card-content__size-item {
  border: 1px solid #E5E5E5;
  border-radius: 12px;
  padding: 8px 16px;
  font-family: "Gilroy";
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #333333;
}
.card-content__bottom {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;
  margin-bottom: 20px;
}
.card-content .card-product__count-button {
  width: 50px;
  height: 50px;
  border-radius: 12px;
}
.card-content .card-product__count-num {
  width: 100%;
  height: 100%;
  border-radius: 12px;
}
.card-content__other {
  font-family: "Circe";
  height: 50px;
  width: 100%;
}
.card-content__other.check {
  background-color: #F1F5FF;
  color: #333333;
}
.card-content .card-product__favourites-icon {
  width: 42px;
  height: 42px;
}
.card-content .card-product__favourites span {
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
}

.tab-descr {
  border: 1px solid #E7E7E7;
  padding: 50px;
  border-radius: 8px;
}
.tab-descr__inner {
  max-width: 1140px;
  columns: 2;
  column-gap: 100px;
}
.tab-descr__item {
  page-break-inside: avoid;
  -moz-column-break-inside: avoid;
  -webkit-column-break-inside: avoid;
  break-inside: avoid;
  break-inside: avoid-column;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  column-gap: 5px;
}
.tab-descr__item:not(:last-child) {
  margin-bottom: 10px;
}
.tab-descr__item-name {
  font-size: 16px;
  font-weight: 600;
  line-height: 22px;
  color: #666666;
  white-space: nowrap;
}
.tab-descr__item-line {
  width: 100%;
  height: 1px;
  border: 1px dashed #919191;
  position: relative;
  bottom: 5px;
}

.tab-content {
  border: 1px solid #E7E7E7;
  padding: 50px;
  border-radius: 8px;
}

.tab-form__rating {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  column-gap: 20px;
}
.tab-form__rating-name {
  font-family: "Circe";
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  color: #919191;
}
.tab-form__rating .rating-area:not(:checked) > label:before {
  width: 29px;
  height: 28px;
}
.tab-form .form__label {
  width: 380px;
}
.tab-form .form__label-textarea {
  width: 600px;
}

.tab-reviews {
  margin-bottom: 50px;
}
.tab-reviews__item {
  border: 1px solid #E7E7E7;
  padding: 50px;
  border-radius: 8px;
}
.tab-reviews__item:not(:last-child) {
  margin-bottom: 20px;
}
.tab-reviews__item-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}
.tab-reviews__item-name {
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
  color: #666666;
}
.tab-reviews__item-date {
  font-size: 16px;
  font-weight: 400;
  line-height: 27px;
  color: #666666;
}
.tab-reviews__item-content {
  margin-top: 32px;
}
.tab-reviews__item-text {
  font-size: 16px;
  font-weight: 400;
  line-height: 27px;
  color: #000;
  -webkit-line-clamp: 3;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.tab-reviews__item-text strong,
.tab-reviews__item-text b {
  font-weight: 700;
}
.tab-reviews__item-text.show {
  -webkit-line-clamp: inherit;
}
.tab-reviews__item-button {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 22px;
  color: #4F649A;
  position: relative;
  padding-right: 20px;
  margin-top: 10px;
}
.tab-reviews__item-button::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 2px;
  width: calc(100% - 20px);
  background-color: #4F649A;
  height: 1px;
}
.tab-reviews__item-button::before {
  background-image: url(../img/arrow/arrow-right.svg);
  width: 10px;
  height: 8px;
}
.tab-reviews__item-button.hide {
  display: none;
}
.tab-reviews .rating-area:not(:checked) > label:before {
  width: 29px;
  height: 28px;
}
.tab-reviews__button {
  padding: 13px 80px;
  height: 50px;
  border-radius: 12px;
  margin-left: auto;
  margin-top: 20px;
}

.registration__content {
  max-width: 900px;
}
.registration__form {
  margin-bottom: 52px;
}
.registration__form-radio {
  margin-bottom: 52px;
}

.reg-comment__title {
  font-family: "Circe";
  font-size: 26px;
  font-weight: 700;
  line-height: 32px;
  margin-bottom: 32px;
}
.reg-comment__textarea {
  height: 100px;
  margin-bottom: 32px;
}
.reg-comment__buttons {
  margin-left: auto;
  width: fit-content;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  column-gap: 28px;
}
.reg-comment__button {
  padding: 0 56px;
}